import { makeStyles } from '@material-ui/core/styles';
import { replaceKeysLabelText } from '../../../../utils/utility';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => {
  return {
    '@global': {
      '.sticky:not(.ghostdiv-reached)': {
        background: 'rgb(247, 247, 247)',
        border: `1px solid ${theme.mixins.White(300)}`,
        boxShadow: `0px 2px 4px 0px ${theme.mixins.Black(110)}`,
        '& .sticky-hidden': {
          display: 'none',
        },
        '& .section-container': {
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
        },
        '& .page-title': {
          paddingTop: theme.spacing(4),
          paddingBottom: theme.spacing(4),
          margin: 0,
        },
        '& .MuiTypography-h2': {
          ...theme.typography.h5Bold,
          margin: 0,
        },
        '& .detail-section': {
          display: 'flex',
          marginBottom: '0',
          width: 'auto',
        },
        '& .cta-wrap': {
          display: 'flex',
          justifyContent: 'space-evenly',
          alignItems: 'center',
          width: '100%',
          paddingTop: theme.spacing(2),
          paddingBottom: theme.spacing(2),
        },
      },
      '.sticky.ghostdiv-reached': {
        position: 'static',
        transform: 'none',
        transition: 'none',
      },
    },
    pageTitle: {
      marginBottom: theme.spacing(3),
      [theme.breakpoints.up('lg')]: {
        marginBottom: theme.spacing(6),
      },
      '& .rating': {
        alignItems: 'center',
        verticalAlign: 'top',
        border: `1px solid ${theme.mixins.Grey(500)}`,
        borderRadius: pxToRem(4),
        ...theme.mixins.marginLeft(pxToRem(10)),
        ...theme.mixins.paddingLR(3, 3),
        [theme.breakpoints.up('lg')]: {
          padding: `${pxToRem(2)} ${pxToRem(5)}`,
          marginTop: theme.spacing(2),
          '.sticky:not(.ghostdiv-reached) &': {
            marginTop: theme.spacing(-0.5),
          },
        },
        '& svg': {
          height: pxToRem(13),
          width: pxToRem(13),
          [theme.breakpoints.up('lg')]: {
            height: theme.spacing(2),
            width: theme.spacing(2),
          },
        },
        '& .MuiTypography-labelRegular': {
          ...theme.typography.body2Bold,
          margin: 0,
        },
      },
    },
    location: {
      marginBottom: theme.spacing(3),
      color: theme.palette.text.tertiary,
      [theme.breakpoints.up('lg')]: {
        marginBottom: theme.spacing(2),
      },
    },
    detailSection: {
      display: 'none',
      [theme.breakpoints.up('lg')]: {
        display: 'flex',
        flexDirection: 'row',
        marginBottom: theme.spacing(5),
        '& > div': {
          '&:not(:first-child)': {
            ...theme.mixins.paddingLeft(theme.spacing(4)),
          },
          '&:not(:last-child)': {
            ...theme.mixins.paddingRight(theme.spacing(4)),
            ...theme.mixins.borderRight(`1px solid ${theme.mixins.Black(200)}`),
          },
        },
        '.sticky:not(.ghostdiv-reached) &:not(.btn-present) .contacts-section': {
          ...theme.mixins.paddingRight(0),
          ...theme.mixins.borderRight('none'),
        },
      },
    },
    ctaWrapper: {
      display: 'none',
      [theme.breakpoints.down('md')]: {
        '& .primaryCTA': {
          whiteSpace: 'nowrap',
        },
      },
      [theme.breakpoints.up('lg')]: {
        display: 'flex',
      },
      '& .secondaryCTA .MuiButton-endIcon': {
        display: 'none',
      },
    },
    contacts: {
      display: 'flex',
      flexDirection: 'column',
      width: '100%',
      paddingBottom: theme.spacing(1),
      [theme.breakpoints.up('lg')]: {
        width: 'auto',
        flexDirection: 'row',
        alignItems: 'center',
        paddingBottom: theme.spacing(0),
      },
    },
    contactTxt: {
      color: props =>
        props.themeColor &&
        replaceKeysLabelText({ value: props.themeColor }, ['1']).value,
      textDecoration: 'none',
      paddingBottom: theme.spacing(3),
      [theme.breakpoints.up('lg')]: {
        display: 'inline',
        ...theme.mixins.marginRight(theme.spacing(4)),
        paddingBottom: '0',
        '&:last-child': {
          ...theme.mixins.marginRight(0),
        },
        '&:hover': {
          color: theme.palette.primary.main,
          textDecoration: 'underline',
        },
      },
    },
    socialIcons: {
      width: '100%',
      [theme.breakpoints.up('lg')]: {
        width: 'auto',
      },
      '& a': {
        ...theme.mixins.marginRight(theme.spacing(2)),
        '&:last-child': {
          ...theme.mixins.marginRight(0),
        },
      },
      '& .MuiIconButton-root': {
        padding: theme.spacing(1),
        backgroundColor: theme.mixins.Grey(100),
        height: theme.spacing(5),
        width: theme.spacing(5),
        '&:hover': {
          backgroundColor: props =>
            props.themeColor &&
            replaceKeysLabelText({ value: props.themeColor }, ['1']).value,
          '& svg': {
            color: theme.palette.primary.contrastText,
          },
        },
        '&.facebookIcon svg': {
          height: pxToRem(20),
          width: pxToRem(20),
        },
        '& svg': {
          color: theme.palette.primary.main,
        },
      },
    },
    modalPopUp: {
      backgroundColor: theme.mixins.White(),
      '& .MuiDialog-scrollPaper': {
        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
      },
      '& .MuiPaper-root': {
        margin: '0',
        height: '100%',
        width: '100%',
        '& > button': {
          padding: 0,
          '& svg': {
            color: theme.mixins.Black(),
            width: theme.spacing(3),
            height: theme.spacing(3),
          },
        },
      },
      '& .MuiBackdrop-root': {
        backgroundColor: 'transparent',
      },
      '& .MuiDialogTitle-root': {
        padding: 0,
      },
      '& .MuiDialogContent-root': {
        margin: `${theme.spacing(8)} ${theme.spacing(3)} 0`,
        padding: 0,
        color: theme.mixins.Black(),
        '& .MuiTypography-h2': {
          ...theme.mixins.marginLeft(0),
        },
        '& button': {
          position: 'absolute',
          bottom: theme.spacing(6),
          ...theme.mixins.left(theme.spacing(0)),
        },
      },
      '& .detail-section': {
        display: 'flex',
        flexDirection: 'column',
      },
      '& .mobile-cta-wrap': {
        display: 'flex',
        position: 'absolute',
        bottom: '0',
      },
    },
  };
});

export default useStyles;
